@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Poppins&display=swap);
/*! minireset.css v0.0.6 | MIT License | github.com/jgthms/minireset.css */
html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

h1,
h2,
h3,
h4,
h5 {
  line-height: 1.2;
}

ul {
  list-style: none;
}
button,
input,
select,
textarea {
  margin: 0;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
img,
video {
  height: auto;
  max-width: 100%;
}
iframe {
  border: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
td:not([align]),
th:not([align]) {
  text-align: left;
}


html {
  line-height: 1.6;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
}




:root {
  --background: #161616;
  --white: #ffffff;
  --toggle: rgb(61, 61, 60);
}

body {
  font-size: 1.3rem;
  font-family: Montserrat;
}

header {
  padding: 0.65em 0;
}

a {
  text-decoration: none;
  /* color: var(--white); */
}

